import React from "react";
import "../styles/Clock.css";
import { useState, useEffect } from "react";

const Clock = () => {
  const timer = useNewTimer(new Date());

  return (
    <div>
      <h1 className="clock">{timer.toLocaleTimeString([], { hour12: true, hour: '2-digit', minute: '2-digit' }).replace("AM","").replace("PM","").replace(/^0(?:0:0?)?/, '')}</h1>
      {/* <h1 className="clock">{timer.toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit' }).replace(/^0(?:0:0?)?/, '')}</h1> */}
    </div>
  );
};

function useNewTimer(currentDate) {
  const [date, setDate] = useState(currentDate);

  useEffect(() => {
    var timerID = setInterval(() => tick(), 1000);
    return function cleanup() {
      clearInterval(timerID);
    };
  });

  function tick() {
    setDate(new Date());
  }

  return date;
}

export default Clock;
